<script>
  import Tab from './tab'  
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  import Modal from 'view/components/modal'
  import Editor from 'view/components/editor'
  import Uploader from 'view/components/uploader'

  export default {
    name: 'update-week',
    data () {
      return {
        week: {},
        id: 0,
        name: '',
        keywords: '',
        description: '',
        abstract: '',
        tab: false,
        tabContent: '',
        tabs: [],

        editor: null,
        loaded: false,
        apiUrl: 'weeks/' + this.$route.params.id + "/upload"
      }
    },
    components: {
      Tab,
      Modal,
      Editor,
      Loading,
      Uploader
    },
    
    async created () {
      try {
        const request = await api.get(`weeks/${this.$route.params.id}`)
        const tabs = await api.post('weeks/tabs')

        this.week = request.data
        this.id = request.data.id
        this.name = request.data.name
        this.keywords = request.data.keywords
        this.description = request.data.description

        this.tabs = tabs

        this.loaded = true
      } catch(error) {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'weeks' })
      }
    },
    methods: {
      handleEditor (editor) {
        this.editor = editor

        this.editor.setContents(this.week.abstract)
      },
      editTab (tab) {
        this.tab = tab
      },
      closeTab () {
        this.tab = false
        this.tabContent = ''
      },
      getTabContent () {
        const content = this.week.tabs.filter(t => t.tab === this.tab).length ?
          this.week.tabs.find(t => t.tab === this.tab).content : ''

        return content
      },
      updateTabContent () {
        const data = {
          week_id: this.week.id,
          tab: this.tab,
          content: this.tabContent.getContents()
        }

        api.post('weeks/tab', data).then(response => {
          this.$store.dispatch('addToastMessage',
            { text: this.$t('week.updated'), type: 'success' }
          )
                    
          this.closeTab()
          this.week = response.data
        }).catch(error => {
          this.$store.dispatch('addToastMessage',
            { text: error.data.errors.content[0] }
          )
        })
      },
      handleTabContent (content) {
        this.tabContent = content
      },

      getImage () {
        const image = this.week.image
          ? [this.week.image.data] 
          : []
          
        return image
      },

      reInitImage (response) {
        this.week = response.data
      },

      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              name: this.name,
              keywords: this.keywords,
              description: this.description,
              abstract: this.editor.getContents()
            }

            api.put(`weeks/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('week.updated'), type: 'success' }
              )
              this.$router.push({ name: 'weeks' })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.weeks.update' | translate">

          <div class="row row-reverse mt-5">
            <div class="col-8">
              <div class="row">
                <div class="col-6">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('name')}">
                    <div class="input-group">
                      <input
                        id="name"
                        name="name"
                        v-model="name"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('name')"></i>
                      <label class="control-label" for="name">{{'week.name'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('name')"
                             class="help text-danger">{{
                        errors.first('name')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col-6">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('keywords')}">
                    <div class="input-group">
                      <input
                        id="keywords"
                        name="keywords"
                        v-model="keywords"/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('keywords')"></i>
                      <label class="control-label" for="keywords">{{'week.keywords'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('keywords')"
                             class="help text-danger">{{
                        errors.first('keywords')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-12">
                  <textarea 
                    id="excerpt"
                    class="col-md-12"
                    v-model="description"
                    :placeholder="'week.description' | translate"
                  >
                  </textarea>
                </div>
              </div>
            </div>

            <div class="col-4">
              <div class="row">
                <Uploader
                  :maxFiles="1"
                  :apiUrl="apiUrl"
                  :images="getImage"
                  @onChange="reInitImage"
                  :placeholder="'week.image' | translate"                
                />
              </div>
            </div>
          </div>

          <div class="row mt-2">
            <Editor @editor="handleEditor"/>
          </div>

          <div class="row row-reverse mt-2">
            <h3 class="pt-2 col-12 text-right">{{ $t('week.tabs') }}</h3>
            <ul style="direction: rtl;text-align: right;">
              <li v-for="(tab, index) in tabs">
                <a class="text-info" @click="editTab(index)">{{ tab }}</a>
              </li>              
            </ul>
          </div>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>

        <modal v-if="tab" @onClose="tab = false">
          <template slot="header">
            <div class="col text-center">
              {{ tabs[tab] }}
            </div>
          </template>

          <template slot="body">
            <Tab 
              :week="week.id" 
              :tab="tab" 
              :content="getTabContent()" 
              @content="handleTabContent"
            />
          </template>

          <template slot="footer">
            <div class="col text-center">
              <button 
                @click="closeTab"
                v-html="$t('buttons.cancel')"
                class="btn btn-info btn-micro"
              ></button>
              <button 
                @click="updateTabContent"
                v-html="$t('buttons.update')"
                class="btn btn-danger btn-micro"          
              ></button>
             </div>
          </template>
        </modal>

      </div>
    </div>
  </div>
</template>
