<script>
  import Editor from 'view/components/editor'

  export default {
    name: 'update-tab',
    data () {
      return {
        editor: null
      }
    },
    
    props: {
	    tab: {
	      type: String,
	      required: true
	    },
	    content: {
	      type: String,
	      required: true
	    }
	  },

    components: {
      Editor
    },

    methods: {
      handleEditor (editor) {
        this.editor = editor

        this.$emit('content', this.editor)
        this.editor.setContents(this.content)
      },
      update () {
        data = {
          tab: this.tab,
          content: this.content
        }

        this.$emit('onUpdate', data)
      }
    }
  }
</script>

<template>
  <div class="form-elements">
    <div class="row">
      <Editor @editor="handleEditor" :editor_id="tab" :mode="'classic'"/>
    </div>
  </div>
</template>

<style>
  .sun-editor-id-editorArea {
    max-height: 200px;
    overflow: scroll;
  }
</style>